import * as React from 'react';

interface IProps {
  loading: boolean;
  block?: boolean;
  style?: React.CSSProperties;
  children: React.ReactNode;
  className?: string;
  loadedClassName?: string;
  useSpace?: boolean;
}

const Bloc: React.FunctionComponent<IProps> = ({
  loading,
  children,
  className,
  loadedClassName,
  block,
  style,
  useSpace,
}) => (
  <>
    <div
      style={style}
      className={`${className ? className : ''}${
        loading
          ? ' skeleton-bloc'
          : loadedClassName
          ? ` ${loadedClassName}`
          : ''
      }${block ? ' block' : ''}${loading && useSpace ? ' full-space' : ''}`}
    >
      {children}
    </div>
  </>
);

export default Bloc;
